import React, { useEffect, useRef, useState } from 'react';
import YouTube from 'react-youtube';
import ReactPlayer from 'react-player/youtube'
import PlayerUtil from '../utils/PlayerUtil';

const YoutubePlayer = ({setTime}) => {
  const playerRef = useRef();
  const [videoId, setVideoId] = useState('T_ULw_3KB8M');
  const [playing, setPlaying] = useState(false);
  const [currtime, setCurrTime] = useState('');
  const opts = {
    height: '400px',
    width: '400px',
    playerVars: {
      autoplay: 1,
      controls: 1,
      autohide:0,
    },
  };
  const OnPlayerStateChange = (event)=>{
    console.log(event.data);
  }
  const onPlaybackRateChange = (event)=>{
    console.log(playerRef.current.getCurrentTime())
  }
  const onPlaybackQualityChange = (event)=>{
    console.log(playerRef.current.getCurrentTime())
  }
  
  const onReady = (event) => {
    playerRef.current = event.target;
  };

  

  const playpauseVideo = (state)=>{
    if(state) {
      playerRef.current.pauseVideo();
      setPlaying(false);
    } else {
      playerRef.current.playVideo();
      setPlaying(true);
    }
  }
  const seekTo = (seconds) => {
    playerRef.current.seekTo(seconds, true);
  };

  const nextVideo = () => {
    // TODO: Implement logic for playing the next video in a playlist.
    setVideoId('uv633g6qoCQ');
  };

  const prevVideo = () => {
    // TODO: Implement logic for playing the previous video in a playlist.
  };
  const handleTime = ()=>{
    setInterval(() => {
      let time = playerRef.current.getCurrentTime();
      setCurrTime(PlayerUtil.toTime(time));
    }, 1000);
  }
  return (
    <div className='PlayerBody'>
      <YouTube
        videoId={videoId}
        opts={opts}
        onReady={onReady}
        onStateChange={(event)=>{OnPlayerStateChange(event)}}
        onPlaybackRateChange={onPlaybackRateChange}
        onPlaybackQualityChange={onPlaybackQualityChange}
        onPlay={handleTime}
      />
      <button onClick={() => seekTo(30)}>Seek to 30 seconds</button>
      <button onClick={nextVideo}>Next video</button>
      <button onClick={prevVideo}>Previous video</button>
      <button onClick={()=>{playpauseVideo(playing)}}>play</button>
    </div>
  );
};

export default YoutubePlayer;