import React from 'react'
import playbtn from '../../../../static/icons/play-trian.svg'
import { useNavigate } from 'react-router-dom'
import { useContext } from 'react'
import { TrackListContext } from '../../../../Context/baseContext'
const ListTrackBlock = ({data}) => {
    const {setTracks} = useContext(TrackListContext);

    const navigate = useNavigate();
  return (
    <div className='track-block' style={{marginLeft:"0"}}>
        <div className='trackindex' onClick={(e)=>{
            setTracks([{TrackId: data.id.videoId, cover:data.snippet.thumbnails.high.url, name:data.snippet.title, artist:data.channelTitle}])
            e.stopPropagation();
            }}>
            <p className='trackindexnum'></p>
            <div className="playtrackbtn">
                <img src={playbtn} alt="" />
            </div>
        </div>
        <div className="thumnail">
            <img src={data.snippet.thumbnails.high.url} alt="" />
        </div>
        <div className="track-info">
            <p className='track-name' onClick={()=>{navigate(`/track/${data.id}`, {state: {id: data.id.videoId, cover:data.snippet.thumbnails.high.url, name:data.snippet.title, artist:data.channelTitle}})}}>{data.snippet.title}</p>
            <p className='track-author' >{data.channelTitle}</p>

        </div>
    </div>
  )
}

export default ListTrackBlock