import axios from "axios"
const Domain = window.location.host;
export default class APIservice{
    static async getPlaylistList() {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/list`).then(response=>{return response.data})
            
    }
    static async getPlaylist(id) {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/playlist/${id}`).then(response=>{return response.data})  
    }
    static async getTrack(id) {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/track/${id}`).then(response=>{return response.data})  
    }
    static async getArtist(id) {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/artist/${id}`).then(response=>{return response.data})  
    }
    static async getUserData(name, password){
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/artist/`).then(response=>{return response.data});
    }
    static async search(query){
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/search/${query}`).then(response=>{return response.data});
    }
}