import React from 'react'
import { useState, useEffect } from 'react'
import axios from 'axios'
const CollectionList = () => {
const [list, setList] = useState([])
  useEffect(()=>{
  axios.get('http://127.0.0.1:8000/api/list').then(response=>{return setList(response.data)})
  },[])
  return (
    <div className='container_block container_block_collection'>
        <div className='section_block section_block_library'>
              {list.map((item, index)=>{
                return <div className='name_playlist_block' key={index}>
                <div className='block_info'>
                    <div className='img_container_block_other'>
                          <img src={item.thumnail} alt="" />
                    </div>
                    <div className='bla_bla'>
                          <p>{item.name}</p>
                    </div>
                </div>

              </div>
              })}
        </div>
    </div>
  )
}

export default CollectionList