import React, { useState } from 'react'
import CModal from '../modal/Modal'
import likebtn from '../../../static/icons/heart.svg'
import addto from '../../../static/icons/add-music.svg'
import ModalBtn from '../modal/ModalBtn'

const TrackModal = ({track_data, setVisible}) => {
  return (
    <div className='editbarcontainer'>
        <div className='edit_container'>
          <div className='edit_img'>
            <div>
              <img src={track_data.cover} alt="" />
            </div>
          </div>
          <div>
            <p style={{margin:"10px"}}>{track_data.name}</p>
          </div>
        </div>
        <div className='editacts'>
          <ModalBtn btn={'Like'} btn_icon={likebtn}/>
          <ModalBtn btn={'Add to Playlist'} btn_icon={addto}/>

        </div>
        <div className='closebtn'>
          <p className='closebtnwd' onClick={()=>{setVisible(false)}}>Close</p>
        </div>
    </div>
  )
}

export default TrackModal