import React from 'react'
import next from '../static/icons/next.svg'
import { useState } from 'react'
import '../static/Styles/player_bar.css'
import YouPlayer from './player'
import { useContext } from 'react';
import { TrackListContext } from '../Context/baseContext';
import { useEffect } from 'react'
import PlayerUtil from '../utils/PlayerUtil'
const PlayerBar = () => {
    const {tracks} = useContext(TrackListContext);
    const [trackInfo, setTrackInfo] = useState({});
    const [trackindex, setTrackIndex] = useState(0)
    const [duration, setDuration] = useState(0);
    const [playing, setPlaying] = useState(false);
    const [currtime, setCurrTime] = useState('00:00');
    const [currtimesecs, setCurrTimeSecs] = useState(0);
    const [durationsecs, setDurationSecs] = useState(0);
    const [open, setOpen] = useState(false);

    useEffect(()=>{
        setTrackInfo({name: tracks[trackindex].name, artist:tracks[trackindex].artist, id: tracks[trackindex].TrackId, cover:tracks[trackindex].cover});
        setTrackIndex(0);
        setPlaying(false)
    }, [tracks])

    useEffect(()=>{
        setDuration(PlayerUtil.toTime(durationsecs));
        console.log(duration);
        console.log(durationsecs);
    }, [durationsecs])

    const bigPlayPause = ()=>{
        setPlaying(!playing)
    }
    const SeekTo =(seconds)=>{
        setCurrTimeSecs(seconds)
    }
    const miniPlayPause = (e)=>{
        e.stopPropagation()
        setPlaying(!playing)
    }
  return (
    <div className='player' onClick={()=>{if(!open){setOpen(true)}}}>
        <div className='wrapper'>
            <div className={open?'details details-open':'details details-close'}>
                <div className={open?'details_inner details-inner-open':'details_inner'}>
                    <div  className={open?'details-inner-img-container details-inner-img-container-open':'details-inner-img-container'} >
                        <div>
                            <img className={open?'music_thumnail_img music_thumnail_img_open':'music_thumnail_img'}   src={trackInfo.cover} alt="" />
                            <YouPlayer setTime={setCurrTime} setDuration={setDuration} setTimeSecs={setCurrTimeSecs} setDurationSecs={setDurationSecs}
                            tracks={tracks} playing={playing} trackInfo={trackInfo}/>
                        </div>
                    </div>
                    <div className={open?'track-info track-info-open':'track-info'}>
                         <div>
                            <div className={open?'track-name-player track-name-open':'track-name'}>
                                {trackInfo.name}
                            </div>
                            <div className={open?'track-artist-player track-artist-open':'track-artist'}>
                                {trackInfo.artist}
                            </div>
                         </div>
                    </div>
                    <div className={open?'main_info':'main_info main_hidden'}>
                    <div className='buttons'>
                    <div className="slider_container slider_container_time">
                                        <div className="current-time">{currtime}</div>
                                         <input type="range" min="1" max={durationsecs} value={currtimesecs} className="seek_slider" onChange={(e)=>{SeekTo(e.target.value)}}/>
                                        <div className="total-duration">{duration}</div>
                    </div>
                    <div className="btns_container">
                                    {/* <div class="random-track info_btn">
                                        <img src={} alt=""/>
                                    </div> */}
                                    <div className="prev-track info_btn">
                                        <img src={next} alt=""/>
                                     </div>
                                     <div className="playpause-track">
                                        <i className={`fa fa-${playing?'play':'pause'}-circle fa-5x`} width="40px" height="40px" onClick={bigPlayPause} ></i>
                                     </div>
                                     <div className="next-track info_btn">
                                        <img src={next} alt=""/>
                                     </div>
                                     {/* <div class="repeat-track info_btn">
                                        <img src="/static/base/icons/repeat-one.svg" alt=""/>
                                     </div> */}
                                    </div>
                    </div>

                    </div>
                    {open?<div className='close-area' onClick={()=>{setOpen(false)}}></div>
                    :<div className='play-pause-btn'>
                    <i className={`fa fa-${playing?'play':'pause'} fa-5x`} onClick={(e)=>{miniPlayPause(e)}}></i>
                </div>}
                </div>
            </div>
        </div>
    </div>
    
  )
}

export default PlayerBar