import React from 'react'
import ModalBtn from '../modal/ModalBtn'
import editicon from '../../../static/icons/pen.svg'
import lockicon from '../../../static/icons/lock.svg'
import profileicon from '../../../static/icons/profile.svg'
import minus from '../../../static/icons/minus.svg'


const PlaylistModal = ({playlist_data, setVisible}) => {
    return (
        <div className='editbarcontainer'>
            <div className='edit_container'>
              <div className='edit_img'>
                <div>
                  <img src={playlist_data.thumnail} alt="" />
                </div>
              </div>
              <div>
                <p style={{margin:"20px"}}>{playlist_data.name}</p>
              </div>
            </div>
            <div className='editacts'>
              <ModalBtn btn={"Edit"} btn_icon={editicon}/>
              <ModalBtn btn={"Make Private"} btn_icon={lockicon}/>
              <ModalBtn btn={"Remove from profile"} btn_icon={profileicon}/>
              <ModalBtn btn={"Delete Playlist"} btn_icon={minus}/>

            </div>
            <div className='closebtn'>
              <p className='closebtnwd' onClick={()=>{setVisible(false)}}>Close</p>
            </div>
        </div>
      )
}

export default PlaylistModal