import React from 'react'
import ListTrackBlock from './ListTrackBlock'

const SearchList = ({data_list}) => {
  
  return (
    <div className='search-list'>
        {data_list.map((item, index)=> {
          return <ListTrackBlock data={item} key={index}/>
        })}
    </div>
  )
}

export default SearchList