import React from 'react'
import heart from '../../../static/icons/heart.svg'
import dots from '../../../static/icons/dots.svg'
import { useContext } from 'react'
import { TrackListContext } from '../../../Context/baseContext'

const PlaylistBtnList = ({data, setVisible}) => {
  const {tracks, setTracks} = useContext(TrackListContext);
  return (
    <div className='act_area'>
    <div className='btn_list'>
        <div className='act_btn_playlist'>
                <i onClick={()=>{setTracks(data)}} className='fa fa-play-circle fa-5x collection_btn'></i>
        </div>
        <div className='act_btn_playlist likedbtn'>
            <img src={heart} alt="" />
        </div>
        <div className='act_btn_playlist editbtn' onClick={()=>{setVisible(true)}}>
            <img src={dots} alt="" />
        </div>
    </div>
    </div>
  )
}

export default PlaylistBtnList