import React from 'react'
import playbtn from '../static/icons/play-trian.svg'
import dots from '../static/icons/dots.svg'
import { useNavigate } from 'react-router-dom'
import { useContext } from 'react'
import { TrackListContext } from '../Context/baseContext'
const TrackBlock = ({data}) => {
    const {setTracks} = useContext(TrackListContext)
    const navigate = useNavigate();
  return (
    <div className='track-block' >
        <div className='trackindex'>
            <p className='trackindexnum'></p>
            <div className="playtrackbtn" onClick={()=>{setTracks([data])}}>
                <img src={playbtn} alt="" />
            </div>
        </div>
        <div className="thumnail">
            <img src={data.cover} alt="" />
        </div>
        <div className="track-info">
            <p className='track-name' onClick={()=>{navigate(`/track/${data.uni_id}`, {state: {id: data.id, uni_id: data.uni_id}})}}>{data.name}</p>
            <p className='track-author' onClick={()=>{navigate(`/artist/${data.artist_uniid}`, {state: {id: data.artist_id, uni_id: data.artist_uniid}})}}>{data.singer}</p>
        </div>
        <div className="actionbtncollection">
            <img src={dots} alt="" />
        </div>
    </div>
  )
}

export default TrackBlock