import { useEffect, useMemo, useState, useContext} from 'react';
import ListTypes from '../components/UI/SearchComs/MySearchInput/ListTypes';
import MySearchInput from '../components/UI/SearchComs/MySearchInput/MySearchInput';
import SearchList from '../components/UI/SearchComs/MySearchInput/SearchList';
import '../static/Styles/search.css'
import axios from 'axios';
import { useQuery } from 'react-query';
import APIservice from '../API/APIservice';
import { searchData } from '../Context/baseContext';
function SearchPage() {
  const {searchRes, setSearchRes} = useContext(searchData);
  const [query, setQuery] = useState('')
  const [delay] = useState(1000);
  const [inProgress, setInProgress] = useState(false);
  const [timer, setTimer] = useState(null);
  const makeRequest = () => {
    if (!inProgress && query!='') {
      setInProgress(true);

      APIservice.search(query).then(function (response) {
        setSearchRes(response);
        console.log(response)
        setInProgress(false);
      
      }).catch(function (error) {
        console.error(error);
      });
    }
  }
  const handleKeyUp = (event) => {
    clearTimeout(timer);
    setTimer(setTimeout(makeRequest, delay));
  }
  return (
    <div>
      <MySearchInput handleKeyUp={handleKeyUp} setQuery={setQuery}/>
      <ListTypes/>
      <SearchList data_list={searchRes}/>
    </div>
  );
}

export default SearchPage;
