import React, { useEffect, useRef, useState } from 'react';
import YouTube from 'react-youtube';
import PlayerUtil from '../utils/PlayerUtil';

const YouPlayer = ({setTime, setTimeSecs, setDurationSecs, tracks, playing, trackInfo}) => {
  const playerRef = useRef();
  const opts = {
    height: '0px',
    width: '0px',
    playerVars: {
      autoplay: 0,
      autohide:0,
      controls:0,
      vq:'small',
    },
  };
  useEffect(()=>{
    if(trackInfo.id){
    playerRef.current.loadVideoById(trackInfo.id,0,'small');}
  }, [trackInfo])
  const OnPlayerStateChange = (event)=>{
    console.log(event.data);
    if(event.data===5){
      playerRef.current.playVideo();
    }
  }
  
  const onReady = (event) => {
    playerRef.current = event.target;
  };
  useEffect(()=>{
    try {
    if(playing) {
        playerRef.current.pauseVideo();
      } else {
        playerRef.current.playVideo();
      }}
      catch{
      }
  }, [playing])

  const seekTo = (seconds) => {
    playerRef.current.seekTo(seconds, true);
  };

  const nextVideo = () => {
    // TODO: Implement logic for playing the next video in a playlist.
  };

  const prevVideo = () => {
    // TODO: Implement logic for playing the previous video in a playlist.
  };
  const handleTime = ()=>{
    setInterval(() => {
      let time = playerRef.current.getCurrentTime();
      setTime(PlayerUtil.toTime(time));
      setTimeSecs(time);
    }, 1000);
  }
  return (
    <div className='PlayerBody'>
      <YouTube
        videoId={'jqbSVBS1x0w'}
        opts={opts}
        onReady={onReady}
        onStateChange={(event)=>{OnPlayerStateChange(event); setDurationSecs(playerRef.current.getDuration())}}
        onPlay={handleTime}
      />
    </div>
  );
};

export default YouPlayer;

